
.section_wrapper {
  display: flex;
  justify-content: center;
 margin-left: auto;
 margin-right: auto;



}
 @media screen and (min-width: 768px) {
   .section_wrapper {
     max-width: 768px;
   }
 }

 @media screen and (min-width: 1280px) {
   .section_wrapper {
     max-width: 1280px;
    flex-basis: calc((1280px - 64px) / 3)
   }
 }

.section {

  padding-top: 40px;
  padding-bottom: 75px;
  margin-left: 20px;
    margin-right: 20px;
}

  @media screen and (min-width: 768px) {
    .section
    {
      padding-top: 80px;
    padding-bottom: 204px;}
  }

    @media screen and (min-width: 1280px) {
    .section{
    padding-top: 80px;
    padding-bottom: 81px;
  }
}
